.about {
   display: flex;
   justify-content: center;
  color: white;
}

.about-wrapper {
    
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 80%;
    background-color: rgba(0, 0, 0, 0.466);
    padding: 20px;
    padding-left: 60px;
    padding-right: 60px;
    border-bottom: 1px solid white;
}

.bio-title {
  font-size: 2rem;
}

.bio-wrapper {
    
}

.tech-wrapper,
.bio-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

}

.skills {
    width: 80%;
    position: relative;
    padding-inline: 2.5rem;
    display: flex;
    justify-content: center;

  
  }
  
  .skills-carousel {
    overflow: hidden;
    width: 100%;
    margin-bottom: 30px;
  }
  .skills-carousel__container {
    display: flex;
    user-select: none;
    margin-left: -10px;
    cursor: grab;
  
    &:active {
      cursor: grabbing;
    }
  }
  
  .skill {
    flex: 0 0 6rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-right: 1rem;
    position: relative;
  
    gap: 0.5rem;
    padding: 0.8rem 0.6rem;
    width: 5rem;
    background-color: inherit;
    border: dotted 1px;
    border-color: inherit;
    
  
    p {
      margin-top: 0.2rem;
      margin-bottom: 0.2rem;
      font-size: 0.6rem;
      text-align: center;
      text-transform: uppercase;
    }
  
    i {
      font-size: 3.5rem;
      text-align: center;
    }
  }
  
  .skills-carousel__prev,
  .skills-carousel__next {
    cursor: pointer;
    width: 2rem;
    height: 2rem;
  
    color: inherit;
    position: absolute;
    left: 0rem;
    top: 50%;
    z-index: 2;
    background-color: transparent;
    font-size: 1rem;
    transform: translateY(-50%);
    border-radius: 50%;
    padding-left: 0.1rem;
    border: none;
    border-color: inherit;
  
    svg {
      width: 50%;
      height: 50%;
      color: inherit;
    }

  }
  .skills-carousel__next {
    right: 0rem;
    left: auto;
    padding-right: 0;
  }