
*,
*:after,
*:before {
    box-sizing: border-box;
}

html {
    min-height: 100vh;
    scrollbar-width: none;
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
    line-height: 1.5;
    background: linear-gradient(rgba(0, 0, 0, 0.452), rgba(0, 0, 0, 0.466)), url('./images/annie-spratt-yI3weKNBRTc-unsplash.jpg');
    background-size: cover;
    font-family: "Raleway", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.2;
}

p {
    line-height: 1.5;
}

section {
    width: 100%;
    height: 100%;
    /*will-change: transform;*/
    
}